//import  from ''	
import * as types from './type'
// ES6types

export default{
//actionsmutationmutation

    /**/
    hideHeader:({commit})=>{
        commit(types.HEAD_SHOW_FAIL);
    },
    showHeader:({commit})=>{
        commit(types.HEAD_SHOW_SUCCESS);
    },
    /*loading*/
    hideLoading:({commit})=>{
        commit(types.HIDE_LOADING)
    },
    showLoading:({commit})=>{
        commit(types.SHOW_LOADING)
    },
    /**/
    hideFooter:({commit})=>{
        commit(types.FOOTER_HIDE);
    },
    showFooter:({commit})=>{
        commit(types.FOOTER_SHOW)
    },


    /*
     ES6

     hideFooter:(context)=>{  //context store 
     //console.log(context);
     context.commit(types.FOOTER_HIDE);
     }, */
    }




