<template>
    <div class="content router-view">
        <div class="banner">
            <ul class="clearfix">
                <li><img src="../assets/images/news1.jpg" alt=""/>
                    <div class="text-box">
                        <h2> </h2>
                        <p> / </p>
                    </div>
                </li>
                <li><img src="../assets/images/news2.jpg" alt=""/>
                    <div class="text-box">
                        <h2> </h2>
                        <p> / </p>
                    </div>
                </li>
                <li><img src="../assets/images/news3.jpg" alt=""/>
                    <div class="text-box">
                        <h2> </h2>
                        <p> / </p>
                    </div>
                </li>
                <li><img src="../assets/images/news4.jpg" alt=""/>
                    <div class="text-box">
                        <h2> 142-130</h2>
                        <p> / </p>
                    </div>
                </li>
            </ul>
            <ol>
                <li class="active"></li>
                <li></li>
                <li></li>
                <li></li>
            </ol>
        </div>
    </div>
</template>
<script>
    export default {
        mounted(){
        this.fnSlider();
    },
    watch:{
        // '$route':function(){
        // 	alert(1);
        // }
    },
    methods:{
        fnSlider(){
            $(function (){
                var mySwipe=new Swipe($('.banner')[0],{
                    auto:2000,
                    continuous:true,
                    stopPropation:true,
                    callback:function (index,element){
                        $('.banner ol li').removeClass('active');
                        $('.banner ol li').eq(index).addClass('active');
                    }
                });
            });
        }
    }
    }
</script>


